html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: BEBAS;
  src: url("../fonts/BEBAS.eot?#iefix") format("embedded-opentype"), url("../fonts/BEBAS.woff") format("woff"), url("../fonts/BEBAS.ttf") format("truetype"), url("../fonts/BEBAS.svg#BEBAS") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: HelveticaNeue;
  src: url("../fonts/HelveticaNeue.eot?#iefix") format("embedded-opentype"), url("../fonts/HelveticaNeue.woff") format("woff"), url("../fonts/HelveticaNeue.ttf") format("truetype"), url("../fonts/HelveticaNeue.svg#HelveticaNeue") format("svg");
  font-weight: normal;
  font-style: normal;
}

body {
  box-sizing: border-box;
  font-family: "HelveticaNeue";
  color: #414141;
}

.black {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 100;
  display: none;
}

.active {
  color: #e8603c !important;
}

.active_link {
  background-color: #e8603c !important;
}

.fix {
  position: fixed;
  background-color: #fff;
  width: 100%;
  z-index: 100;
}

.active_menu .nav_menu_line:nth-child(1) {
  transform: rotate(45deg);
  position: relative;
  top: 13px;
}

.active_menu .nav_menu_line:nth-child(2) {
  transform: rotate(-45deg);
  position: relative;
  top: 2px;
}

.active_menu .nav_menu_line:nth-child(3) {
  transform: rotate(180deg);
  opacity: 0;
}

.active_nav {
  position: fixed;
  top: 70px;
  width: 100%;
  display: block !important;
  z-index: 100;
}

.active_nav .nav_list_item {
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  text-align: center;
}

.active_nav .nav_list_item:hover {
  background-color: #f2ae9c;
}

.active_nav .nav_list_item:hover a {
  color: #fff;
}

.margin {
  margin-top: 75px !important;
  margin-bottom: 50px !important;
}

.nav {
  height: 75px;
}

.nav_center {
  width: 69%;
  height: 100%;
  margin: 0 auto;
  display: inline-block;
  margin-left: .5%;
}

@media only screen and (max-width: 920px) {
  .nav_center {
    width: 100%;
    margin-left: 0;
  }
}

.nav_left {
  float: left;
  width: 15%;
  height: 100%;
}

@media only screen and (max-width: 920px) {
  .nav_left {
    width: 0;
  }
}

.nav_right {
  float: right;
  width: 15%;
  height: 100%;
}

@media only screen and (max-width: 920px) {
  .nav_right {
    width: 0;
  }
}

.logo {
  width: 10%;
  line-height: 2;
  text-transform: uppercase;
  font-family: "BEBAS";
  float: left;
  font-size: 22.5px;
  font-size: 2.25rem;
  color: #e8603c;
}

.logo .logo_light {
  color: #f2ae9c;
}

.nav_menu {
  display: none;
  margin: 0 auto;
  position: absolute;
  cursor: pointer;
  right: 20px;
  top: 10px;
  width: 50px;
  height: 50px;
  border: 1px solid #e8603c;
  border-radius: 3px;
}

@media only screen and (max-width: 720px) {
  .nav_menu {
    display: block;
  }
}

.nav_menu .nav_menu_line {
  width: 90%;
  transition: .5s ease all;
  height: 2px;
  display: block;
  margin: 10px auto;
  background-color: #e8603c;
}

.nav_list {
  width: 90%;
  text-align: right;
  float: right;
}

@media only screen and (max-width: 720px) {
  .nav_list {
    display: none;
  }
}

.nav_list_item {
  display: inline-block;
  margin-left: 5%;
}

.nav_list_link {
  text-decoration: none;
  color: #b9b9b9;
  text-transform: uppercase;
  font-size: 10px;
  font-size: 1rem;
  letter-spacing: 1px;
  font-family: "BEBAS";
  line-height: 4.5;
}

.header {
  height: 60px;
}

.header_left {
  background-color: #f2ae9c;
  float: left;
  width: 15%;
  height: 100%;
}

@media only screen and (max-width: 920px) {
  .header_left {
    width: 0;
  }
}

.header_right {
  background-color: #f2ae9c;
  float: right;
  width: 15%;
  height: 100%;
}

@media only screen and (max-width: 920px) {
  .header_right {
    width: 0;
  }
}

.header_center {
  background-color: #e8603c;
  width: 69%;
  height: 100%;
  margin: 0 auto;
  display: inline-block;
  margin-left: .5%;
}

@media only screen and (max-width: 920px) {
  .header_center {
    width: 100%;
    margin-left: 0;
  }
}

.header_title {
  color: #fff;
  font-family: "BEBAS";
  font-size: 15px;
  font-size: 1.5rem;
  line-height: 2.3;
  margin-left: 1.5%;
}

.slider {
  width: 100%;
  max-width: 700px;
  height: 300px;
  overflow: hidden;
  position: relative;
}

.slider .slider_wrap {
  width: 9999px;
  transition: .5s ease all;
}

.slider .slider_wrap .slider_item {
  width: 100%;
  max-width: 700px;
  height: 300px;
  float: left;
}

.slider .slider_wrap .prev {
  position: absolute;
  height: 120px;
  left: 0;
  top: 90px;
  cursor: pointer;
}

.slider .slider_wrap .next {
  position: absolute;
  height: 120px;
  right: 0;
  top: 90px;
  cursor: pointer;
}

.blog {
  height: 100%;
  margin-top: 25px;
}

.blog:after {
  content: " ";
  display: table;
  clear: both;
}

.blog_center {
  width: 69%;
  height: 100%;
  margin: 0 auto;
}

.blog_center:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 920px) {
  .blog_center {
    width: 98%;
  }
}

.blog_item_article {
  width: 71%;
  float: left;
  position: relative;
}

@media only screen and (max-width: 720px) {
  .blog_item_article {
    width: 100%;
  }
}

.blog_item_article:after {
  content: "";
  width: 75px;
  height: 75px;
  background-image: url("../img/article_icon.jpg");
  position: absolute;
  top: 0;
  left: -85px;
}

@media only screen and (max-width: 1080px) {
  .blog_item_article:after {
    left: -80px;
  }
}

@media only screen and (max-width: 920px) {
  .blog_item_article:after {
    width: 0;
  }
}

.blog_item_picture {
  width: 100%;
  height: 300px;
  background-size: 140% 180%;
  background-repeat: no-repeat;
}

.blog_item_picture__bgi_1 {
  background-size: 140% 180%;
  background-repeat: no-repeat;
  background-position: -80px -80px;
  background-image: url("../img/blog_img.jpg");
}

.blog_item_info {
  width: 15%;
  float: left;
  margin-top: 25px;
}

.blog_item_info h6 {
  font-family: "BEBAS";
  text-transform: uppercase;
  color: #e8603c;
  font-size: 7.5px;
  font-size: 0.75rem;
}

.blog_item_info p {
  color: #bababa;
  font-size: 7.5px;
  font-size: 0.75rem;
  margin: 5px	0 10px 0;
}

.blog_item_content {
  width: 80%;
  float: right;
  margin-top: 25px;
}

.blog_item_content:after {
  content: " ";
  display: table;
  clear: both;
}

.blog_item_content h4 {
  font-size: 11.25px;
  font-size: 1.125rem;
  text-transform: uppercase;
  color: #e8603c;
  font-family: "BEBAS";
  letter-spacing: 100%;
}

.blog_item_content p {
  margin: 20px 0 20px 0;
  color: #414141;
  line-height: 18px;
  font-weight: 500;
  font-size: 7.5px;
  font-size: 0.75rem;
}

.gradient {
  height: 15px;
  background-image: repeating-linear-gradient(-45deg, lightgrey 0px, lightgrey 1px, white 1px, white 3px);
}

.blog_others {
  width: 25%;
  float: right;
}

@media only screen and (max-width: 720px) {
  .blog_others {
    width: 100%;
  }
}

.blog_others .gradient {
  float: right;
  width: 60%;
}

.blog_others .gradient:after {
  content: " ";
  display: table;
  clear: both;
}

.blog_others p {
  font-size: 7.5px;
  font-size: 0.75rem;
  color: #414141;
  display: block;
  padding: 35px 0 35px 0;
}

.blog_others .blog_others_item_caption {
  float: left;
  width: 40%;
  font-family: "BEBAS";
  color: #e8603c;
  font-size: 8.75px;
  font-size: 0.875rem;
  letter-spacing: 1px;
}

.blog_others_item:after {
  content: " ";
  display: table;
  clear: both;
}

.blog_others_list {
  padding-top: 35px;
  margin-bottom: 35px;
}

.blog_others_list_item {
  font-size: 7.5px;
  font-size: 0.75rem;
}

.blog_others_list_item a {
  color: #414141;
  text-decoration: none;
}

.blog_others_photo_wrap {
  width: 100%;
  padding-top: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  align-content: flex-start;
}

.blog_others_photo_item_wrap {
  min-width: 50px;
  width: 20%;
  cursor: pointer;
  height: 50px;
  margin: 3px;
  display: inline-block;
  background-color: #f2ae9c;
  border: 1px solid #e8603c;
}

.blog_others_photo_item {
  width: 100%;
  height: 100%;
  z-index: 101;
}

.blog_others_photo__big {
  position: fixed;
  top: 50px;
  left: 10%;
  width: 80%;
  height: 80%;
  z-index: 101;
  display: none;
}

.close {
  position: fixed;
  top: 20px;
  right: 2%;
  width: 50px;
  height: 50px;
  cursor: pointer;
  z-index: 1000;
}

.gradient__main {
  width: 71%;
  margin: 50px 0 50px 0;
  display: block;
}

@media only screen and (max-width: 720px) {
  .gradient__main {
    width: 100%;
  }
}

.blog_wrap {
  margin: 50px 0 50px 0;
}

.blog_wrap:after {
  content: " ";
  display: table;
  clear: both;
}

.blog_item_picture__bgi_2 {
  background-image: url("../img/blog_img_2.jpg");
  background-size: 110% 100%;
  background-repeat: no-repeat;
  background-position: center center;
}

.blog_item_article_text:after {
  content: "";
  width: 75px;
  height: 75px;
  background-image: url("../img/article_icon_2.jpg");
  position: absolute;
  top: 0;
  left: -85px;
}

@media only screen and (max-width: 1080px) {
  .blog_item_article_text:after {
    left: -80px;
  }
}

.blog_item_article__quote {
  width: 70%;
  height: 100%;
  float: none;
}

.blog_item_article__quote:after {
  content: "";
  width: 75px;
  height: 75px;
  background-image: url("../img/quote_icon.jpg");
  position: absolute;
  top: 0;
  left: -85px;
}

@media only screen and (max-width: 1080px) {
  .blog_item_article__quote:after {
    left: -80px;
  }
}

.blog_item_quote {
  width: 100%;
  height: 100%;
  padding: 25px 20px 25px 20px;
  background-color: #f1f1f1;
}

.blog_item_quote p {
  font-size: 8.125px;
  font-size: 0.8125rem;
  font-color: #b9b9b9;
  font-style: italic;
  line-height: 18px;
}

.blog_item_quote_left {
  display: inline-block;
  width: 10%;
  float: left;
  text-align: center;
  height: 100%;
  font-family: "Georgia";
  font-size: 45px;
  font-size: 4.5rem;
  color: #e8603c;
}

.blog_item_content_left {
  margin-top: 0;
  width: 100%;
  display: block;
  height: 100%;
  float: none;
}

.blog_item_info_left {
  margin-top: 0;
  display: block;
  float: none;
}

.blog_item_content__big {
  width: 100%;
  margin-top: 0;
  float: none;
}

.blog_item_content__big p {
  margin-bottom: 0;
}

.blog_item_article__clear {
  width: 70%;
  height: 100%;
  float: none;
}

.blog_item_article__clear:after {
  background-image: url("../img/article_icon_3.jpg");
}

.blog_pages {
  width: 100%;
  height: 100%;
  margin-bottom: 50px;
}

.blog_pages_link {
  display: inline-block;
  width: 25px;
  height: 30px;
  background-color: #f2ae9c;
  color: #fff;
  text-align: center;
  text-decoration: none;
  line-height: 2;
  font-family: "BEBAS";
}
